import React from "react";
import ReactDOM from "react-dom";

import { isProduction } from "./common/utils/isProduction";

import { appLogger } from "./common/utils/createLogger";

if (isProduction()) {

    if ("serviceWorker" in navigator) {
        navigator.serviceWorker.register("/sw.js").catch((error) => {
            appLogger.error("Installing Service Worker Failed", error);
        });

        // @ts-ignore
        navigator.serviceWorker.addEventListener("controllerchange", (event) => {
            if (confirm("Application was updated, do you want to reload.")) {
                document.location.reload();
            }
        });
    }
}

(async () => {
    const { Application } = await import(
        /* webpackChunkName: "Application" */
        "./Application"
    );
    ReactDOM.render(<Application />, document.getElementById("root"));
})();
